import React from "react";
import styled from "styled-components";
import PropTypes from "prop-types";

export const Input = ({ type, status, label}) => {
    switch (status) {
        case "success":
            status ='input-success';
            break;
        case "info":
            status ='input-info';
            break;
        case "alert":
            status ='input-alert';
            break;
        case "warning":
            status ='input-warning';
            break; 
        default:
            status = '';
            break;
    }
    return(
        <UIinput type={type} className={`input ${status}`}>
            {label}
        </UIinput>
    )
}

Input.propTypes = {
    type: PropTypes.oneOf(['text', 'number', 'phone']).isRequired,
    status: PropTypes.oneOf(['Success', 'Info', 'Alert', 'Warning']).isRequired,
    label: PropTypes.string.isRequired
}

Input.defaultProps = {
    type: 'text',
    status: 'Success',
    label: 'Default Input Label'
}

const UIinput = styled.input``;

